* {
    padding: 0;
    margin: 0;
}
.flexbox {
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
  display: flex;

  -webkit-flex-flow: row wrap;
  flex-flow: row wrap;

  font-weight: bold;
  text-align: center;
}

.flexbox > * {
  padding: 10px;
  flex: 1 100%;
}

.header {
  background: black;
  color: white;
}

.footer {
  background: lightgreen;
}

.main {
  text-align: left;
  background: url("../images/living_room.jpg");
  background-repeat: no-repeat;
  background-size: 100;
  background-position: center;
  height: 100vh;
}

// .aside {
//   background: gold;
// }

@media all and (min-width: 600px) {
  .aside { flex: 1 0px; }
}

@media all and (min-width: 800px) {
  .main    { flex: 3 0px; }
  .aside { order: 1; }
  .main    { order: 2; }
  .footer  { order: 3; }
}


.navigation {
  list-style: none;
  margin: 0;

  background: black;

}

.navigation a {
  text-decoration: none;
  display: block;
  padding: 1em;
  color: white;
}

.navigation a:hover {
  background: darken(black, 2%);
}


.navigation a {
  text-align: center;
  padding: 10px;
  border-top: 1px solid rgba(255,255,255,0.3);
  border-bottom: 1px solid rgba(0,0,0,0.1);
}
